* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.section {
  margin: 40px;
  display: flex;
  justify-content: space-between;
  gap: 50px;
}
.section__image-container {
  max-width: 505px;
}
.section__image {
  max-width: 100%;
  height: auto;
}
@media (max-width: 768px) {
  .section {
    flex-direction: column-reverse;
  }
}

.description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  gap: 20px;
}
.description__title {
  font-size: 42px;
}
@media (max-width: 600px) {
  .description__title {
    font-size: 32px;
  }
}
.description__paragraph {
  font-size: 16px;
}
@media (max-width: 600px) {
  .description__paragraph {
    font-size: 14px;
  }
}

.button {
  padding: 5px 20px;
  border: none;
  border-radius: 20px;
  background-color: rgb(227, 193, 176);
  color: rgb(255, 255, 255);
  font-weight: 700;
}

body {
  font-family: "Roboto", sans-serif;
}

.wrapper {
  max-width: 1440px;
  margin: 0 auto;
}